:root {
  --green: #4ecdc4;
  --darkGrey: #292f36;
  --white: #fffffa;
  --black: #000103;
  --orange: #fa8334;
  --red: #ff312e;
  --violet : #6f0c8d;
}
/* titip //pagecolor="#ffffff" */

html {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  /* background-image: url(../images/floating-cogs.svg); */
  background-image: url(/static/media/3526768.0da91477.jpg);
  background-color: #292f36;
  background-color: var(--darkGrey);
  min-height: calc(100vh - 100px);
  margin: 10px;
  background-attachment: fixed;
  background-size: inherit;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.logo-container {
    grid-area: logo;
    -webkit-animation: logo-spin infinite 5s linear;
            animation: logo-spin infinite 5s linear;
}

.slow {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.fast {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.logo {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

@-webkit-keyframes logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@keyframes logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
.title {
  grid-area: title;
  display: grid;
  grid-gap: 20px;
  width: 100%;
  max-width: 700px;
  grid-template-columns: 1fr auto 1fr;
  -webkit-align-items: center;
          align-items: center;
}

/*
Thanks to Wes Bos for the gradient trick
https://cssgrid.io
*/
.title:before,
.title:after {
  display: block;
  content: "";
  height: 10px;
}

.title:before {
  background: linear-gradient(to left, var(--violet), transparent);
}

.title:after {
  background: linear-gradient(to right, var(--violet), transparent);
}

.notification {
  border-radius: 4px;
  display: grid;
  grid-gap: 10px;
  grid-column: 1 / -1;
  grid-row: 1 / -1;
  grid-template-columns: 1fr 5fr;
  border: 1px solid var(--black);
  position: relative;
  transition: 0.35s;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}

.notification.visible {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.notification.success {
  background-color: var(--green);
}

.notification.error {
  background-color: var(--red);
}

.notification img {
  justify-self: center;
  -webkit-align-self: center;
          align-self: center;
  width: 40px;
}

.notification p {
  color: var(--black);
  font-weight: 500;
  -webkit-align-self: center;
          align-self: center;
  padding-right: 10px;
}

.notification:hover {
  border-color: var(--black);
}

.description {
  overflow: hidden;
  grid-area: description;
  display: grid;
  font-size: 16px;
  font-weight: 500;
  max-width: 700px;
  grid-template-columns: 1fr;
}

.description-text {
  grid-column: 1 / -1;
  grid-row: 1 / -1;
}

.social-links {
  grid-area: social;
  align-self: end;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
  justify-items: center;
  grid-gap: 30px;
}

.social-link {
  color: var(--white);
  grid-column: minmax(50px, 1fr);
  font-weight: 500;
  display: grid;
  grid-gap: 10px;
  justify-items: center;
  text-decoration: none;
  transition: all 0.3s ease-in;
}

.social-link:hover {
  -webkit-filter: brightness(85%);
          filter: brightness(85%);
  text-decoration: underline;
}

.social-logo {
  width: 60px;
}

.social-text {
  font-size: 16px;
}

@media (max-width: 700px) {
  .social-links {
    margin-top: 20px;
    grid-gap: 10px;
  }
}

.countdown {
  grid-area: countdown;
  display: grid;
  grid-template-columns: repeat(4, minmax(50px, 150px));
  grid-gap: 10px;
  padding-bottom: 10px;
  -webkit-justify-content: center;
          justify-content: center;
}

.countdown div:not(:last-child) {
  border-right: 2px solid var(--violet);
}

.countdown-segment {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr;
}

.countdown-segment-number {
  font-size: 30px;
  font-weight: 700;
  transition: all 0.2s ease-in;
}

.countdown-segment-number:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.countdown-segment-caption {
  font-size: 16px;
  font-weight: 500;
}

@media (max-width: 500px) {
  .countdown {
    border-bottom: 5px solid var(--violet);
  }
  .countdown div:not(:last-child) {
    border-right: 0px;
  }
}

.subscribe {
  grid-area: subscribe;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: [input] 4fr [button] 1fr;
}

.subscribe-email {
  grid-area: input;
  border-radius: 4px;
  height: 40px;
  background-color: var(--black);
  border: 1px solid var(--white);
  color: var(--white);
  padding-left: 10px;
  outline: none;
  font-size: 16px;
  transition: all 0.2s ease-in;
}

.subscribe-email::-webkit-input-placeholder {
  color: var(--white);
}

.subscribe-email::-ms-input-placeholder {
  color: var(--white);
}

.subscribe-email::placeholder {
  color: var(--white);
}

.subscribe-email:focus,
:hover {
  border-color: var(--violet);
}

.subscribe-button {
  grid-area: button;
  height: 40px;
  background-color: var(--violet);
  color: var(--white);
  border: 1px solid var(--black);
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.subscribe-button:hover,
.subscribe-button:focus {
  outline: none;
  -webkit-filter: brightness(85%);
          filter: brightness(85%);
}

@media (max-width: 700px) {
  .subscribe {
    grid-template-areas:
      "input"
      "button";
  }
  .subscribe-button,
  .subscribe-email {
    width: 250px;
    grid-column: 1 / -1;
  }
}

.background {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr;
  grid-template-rows: 100px auto 50px auto 100px auto;
  grid-template-areas:
    "countdown"
    "logo"
    "title"
    "description"
    "subscribe"
    "social";
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  color: var(--white);
}

